import React, { useState, useEffect } from "react";
import { searchlocation,listCity,listSearchByRestaurent,list } from "./apiCore"; 
import Card from "./Card";
import { Link } from "react-router-dom";


const Location = () => {


    const [data, setData] = useState({
                                        cities: [],
                                        city: "",
                                        restaurentName:"",
                                        restaurents:[],
                                        search: "",
                                        results: [],
                                        searched: false
                                    });
    const { cities, restaurents } = data;

    const loadCities = () => {
        listCity().then(data => {
            if (data.error) {
                console.log(data.error);
            } else {
                setData({ ...data, cities: data});
            }
        });
    };
   
    

    useEffect(() => {
        loadCities();
    }, []);
    
    const changeSelectOptionHandler = (event) => {        
        searchlocation({  city: event.target.value  }).then(
            response => {
                    if (response.error) {
                        console.log(response.error)
                    } 
                    else {
                        setData({ ...data, restaurents: response, searched: true });
                    }}
            );
    };

const handlelocation = name => event => {
        setData({ ...data, [name]: event.target.value, searched: false });
};



const searchedLocations = (restaurents = []) => {
        return  (
            <select  className="col-md-4 mr-4 mb-3"   onChange={handlelocation("restaurentName")} style={{padding:"10px 20px"}}>
                  <option>Select Restaurent </option>
                    {restaurents.map((foodproduct, i) => (
                        <option key={i}  >
                             {foodproduct.restaurantName} 
                        </option>
                    ))}
            </select>
           
        );
};
    const searchForm = () => (
        <div >
            <form>
                <div className="row">
                    <select className="col-md-4  mb-3 mr-4 " onChange={changeSelectOptionHandler} style={{padding:"10px 20px"}}>
                        <option value="All">Select City</option>
                            {cities.map((c, i) => (
                                <option key={i} value={c._id}>{c}</option>
                        ))}
                    </select>
                        {searchedLocations(restaurents)}
                    <Link className="btn  btn-rounded-white mb-3 ml-3" to="/Shop"id="signup">
                        Search
                    </Link>
                </div>
            </form>
        </div>
        
    );

    return (
        <div className="row">            
            <div className="container mb-3 ml-3">{searchForm()}</div>
        </div>
    );
};

export default Location;