import React from "react";
import "../../styles/Layout.css"



const Layout = ({title="Title",description="Description",className, children})=>
(   
    <div className="container-fluid cover-img ">
        <br/>
        <div className="container">
            <div  style={{color: "black", boxShadow: '1px 2px 9px #F4AAB9',
            backgroundColor:"white", padding:"20px",borderRadius:"25px"}}>

            <h2 className="title" >{title}</h2>
            <p className="lead">{description}</p>
            </div>
            <br/>
           
            
            
            
        </div>
       
        <div style={{paddingBottom: "50px"}} className={className}>{children}</div>
    </div>
)


export default Layout;