import React, { useState, useEffect }from "react";
import {getFoodProducts,searchByCity} from"./apiCore";
import Card from "./Card";

import Location from "./Location";
import Mcard from "./Mcard";
import "../../styles/Layout.css";
import "../../styles/home.css"


const Home =()=>  {
    const [foodproductsBySell, setFoodProductsBySell] = useState([]);
    const [foodproductsByArrival, setFoodProductsByArrival] = useState([]);
    const [error, setError] = useState(false);
    
    

    const loadFoodProductsBySell = () => {
        getFoodProducts('sold').then(data => {
            if (data.error) {
                setError(data.error);
            } else {
                setFoodProductsBySell(data);
            }
        });
    };

    const loadFoodProductsByArrival = () => {
        getFoodProducts('createdAt').then(data => {
           
            if (data.error) {
                setError(data.error);
            } else {
                setFoodProductsByArrival(data);
            }
        });
    };
    

    useEffect(() => {
        loadFoodProductsByArrival();
        loadFoodProductsBySell();
       

    }, [])
    
    
   

    return (
        
        <div className="myStyle">
            <div className="container-fluid story-page" >
                <div className="container-fluid home-page ">
                    <div className="container-fluid cover-img">
                        <div className="container cover-img-top-part">
                            <nav className="navbar">
                                <div className="nav-links" id="navLinks">
                                    {/* <ul>
                                        <li>
                                            <Link className="nav-link" to="/signin">Login</Link>
                                        </li>
                                        <li className="signup1">
                                            <Link className="btn btn-rounded nav-link" to="/signup" id="signup">
                                                Create an account
                                            </Link>
                                        </li>
                                    </ul> */}
                                </div>
                            </nav>
                            <div className="card-body one"style={{paddingBottom:"100px"}}>
                                <div className="logo">
                                    <b>e!</b>
                                </div> 
                                <h1 className="card-title main-heading" style={{paddingBottom:"30px"}}>Find the best restaurants, cafés, and bars</h1>
                                {/* <form name="form1" className="form-group1">
                                         <div className="form-group2">
                                            <input type="search" className="location" placeholder="Please type a location"  />
                                            <div className="resturant">
                                                <i className="bi bi-search" />
                                            <input className="Field" type="text" placeholder="Search for restaurants" />
                                            </div>
                                        </div> 
                  
                                </form> */}
                                <Location/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
                    
                    {/* to create quick search items */}
            <div className="container">
                <br/>
                <br/>
                <h2  className="quick-search">Quick Searches</h2>
                <h4 className="quick-sub">Discover restaurants by type of meal</h4>
                <div className=" row">
                    <Mcard img="image1" h3="Breakfast"  msubContent="Start your day with exclusive breakfast options" />
                    <Mcard  img="image2" h3="Lunch"  msubContent="Start your day with exclusive breakfast options" />
                    <Mcard img="image3" h3="Dinner" msubContent="Start your day with exclusive breakfast options" />
                    <Mcard img="image4" h3="Snacks" msubContent="Start your day with exclusive breakfast options" />
                    <Mcard img="image5" h3="Drinks" msubContent="Start your day with exclusive breakfast options" />
                    <Mcard img="image6" h3="Nightlife" msubContent="Start your day with exclusive breakfast options" />
                </div>
            </div>

            {/* for New Arrival and Best seller */}
            
            <div className="container ">
                <h2 className="mb-4 mt-4">New Arrivals</h2>
                <div className="row">
                    {foodproductsByArrival.map((foodproduct, i) => (
                        <div key={i} className="col-lg-4 col-md-6 col-sm-12  mt-3">
                            <Card foodproduct={foodproduct} />
                        </div>
                    ))}
                </div>

                <h2 className="mb-4">Best Sellers</h2>
                <div className="row">
                    {foodproductsBySell.map((foodproduct, i) => (
                        <div key={i} className="col-lg-4 col-md-6 col-sm-12  mt-3">
                            <Card foodproduct={foodproduct} />
                        </div>
                    ))}
                </div>
            </div>
            
        </div>
    );
};







export default Home;