import React,{useState, useEffect} from "react";
import Layout from "../core/Layout";
import { isAuthenticated } from "../auth";
//import { Link } from "react-router-dom";
import { createFoodProduct,getCategories } from "./apiAdmin";



const AddFoodProduct=()=>{
    
    const {user,token}=isAuthenticated();
    const [values,setValues]= useState({
        name:'',
        restaurantName: '',
        restaurantAddress:'',
        city:'',
        description:'',
        price:'',
        foodImage: '',
        category :'',
        categories:[],
        loading : false,
        error:'',
        createdFoodProduct: '',
        redirectToProfile :false,
        formData : ''  

    })

    const {
        name,
        restaurantName,
        restaurantAddress,
        city,
        description,
        price,
        sold,
        categories,
        loading,
        error,
        createdFoodProduct,
        redirectToProfile,
        formData
    } =values;

    const init = () => {
        getCategories().then(data => {
            if (data.error) {
                setValues({ ...values, error: data.error });
            } else {
                setValues({
                    ...values,
                    categories: data,
                    formData: new FormData()
                });
            }
        });
    };

    useEffect(() => {
        init();
    }, []);

    const handleChange = name => event => {
        const value = name === 'foodImage' ? event.target.files[0] : event.target.value;
        formData.set(name, value);
        setValues({ ...values, [name]: value });
    };

    const clickSubmit = event => {
        event.preventDefault();
        setValues({ ...values, error: '', loading: true });

        createFoodProduct(user._id, token, formData).then(data => {
            if (data.error) {
                setValues({ ...values, error: data.error });
            } else {
                setValues({
                    ...values,
                    name: '',
                    restaurantName:'',

                    restaurantAddress:'',
                    city:'',
                    description: '',
                    foodImage: '',
                    price: '',
                    sold:'',
                    loading: false,
                    createdFoodProduct: data.name
                });
            }
        });
    };

    

    const newFoodForm =()=>(
        <form action="" className="mb-3" onSubmit={clickSubmit}>
            <h4 className="text-white">
                Add Image
            </h4>
            <div className="form-group">
                <label className="btn btn-secondary">
                    <input onChange={handleChange('foodImage')}type="file"  name="foodImage"accept="image/*"/>
                </label>
            </div>



            <div className="form-group">
                <label className="text-white">Name</label>
                <input onChange={handleChange('name')} type="text" className="form-control" value={name} />
            </div>


            <div className="form-group">
                <label className="text-white">Restaurant Name</label>
                <input onChange={handleChange('restaurantName')} type="text" className="form-control" value={restaurantName} />
            </div>


            <div className="form-group">
                <label className="text-white">Restaurant Address</label>
                <input onChange={handleChange('restaurantAddress')} type="text" className="form-control" value={restaurantAddress} />
            </div>
            <div className="form-group">
                <label className="text-white">Restaurant City</label>
                <input onChange={handleChange('city')} type="text" className="form-control" value={city} />
            </div>

            <div className="form-group">
                <label className="text-white">Description</label>
                <textarea onChange={handleChange('description')} className="form-control" value={description} />
            </div>

            <div className="form-group">
                <label className="text-white">Price</label>
                <input onChange={handleChange('price')} type="number" className="form-control" value={price} />
            </div>
            <div className="form-group">
                <label className="text-white">Sold</label>
                <input onChange={handleChange('sold')} type="number" className="form-control" value={sold} />
            </div>

            <div className="form-group">
                <label className="text-white">Category</label>
                <select onChange={handleChange('category')} className="form-control">
                    <option>Please select</option>
                    {categories &&
                        categories.map((c, i) => (
                            <option key={i} value={c._id}>
                                {c.name}
                            </option>
                        ))}
                </select>
            </div>

           

            <button className="btn btn-outline-primary">Create Product</button>
            
        </form>
    )
    

    const showError = () => (
        <div className="alert alert-danger" style={{ display: error ? '' : 'none' }}>
            {error}
        </div>
    );

    const showSuccess = () => (
        <div className="alert alert-info" style={{ display: createdFoodProduct ? '' : 'none' }}>
            <h2>{`${createdFoodProduct}`} is created!</h2>
        </div>
    );

    const showLoading = () =>
        loading && (
            <div className="alert alert-success">
                <h2>Loading...</h2>
            </div>
        );

    return(
        <Layout
            title="Add a new Food Item"
            description={`G'day ${user.name}, ready to add a new Food Product?`}
        >
            <div className="row">
                <div className="col-md-8 offset-md-2">
                    <div className="col-md-8 offset-md-2">
                        {showLoading()}
                        {showSuccess()}
                        {showError()}
                        {newFoodForm()}
                    </div>
                </div>
            </div>
        </Layout>
    )
} 
export default AddFoodProduct;







