import React from 'react';

import { Link } from 'react-router-dom';

import "../../styles/Mcard.css"




            
const Mcard = (props) => {
  const selectCategory=(event,category)=>{
   localStorage.setItem("category",category)
   

}
  


  return(
   
        

<div className="col-lg-4 col-md-6 col-sm-12  mt-3 qs-col" onClick={(e) => selectCategory(e,JSON.stringify("637f8fd4ab5750af351862e4") )}>
<Link style={{textDecoration: 'none'}} to="/Shop"className="card">

    <div className="card-horizontal qs-col">
        <div className="img-square-wrapper">
        <div className={props.img} >
        </div>
        <div className="card-body" id="card-body-one">
            <h3  className="card-title">{props.h3}</h3>
            <p className="card-text">{props.msubContent}</p>
        </div>
    </div>
    
</div>

</Link>

  </div>
 

           
  )          
  
  }

export default Mcard          
            
            