import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import Layout from './Layout';
import { getCart } from './cartHelpers';
import Card from './Card';
import Checkout from './Checkout';

const Cart = () => {
    const [items, setItems] = useState([]);
    const [run, setRun] = useState(false);

    useEffect(() => {
        setItems(getCart());
    }, [run]);

    const showItems = items => {
        return (
            <div>
                <br/>
                <h3 className='mb-5 ml-2'>Your cart has {`${items.length}`} items</h3>
                
                {items.map((foodproduct, i) => (
                    <Card
                        key={i}
                        foodproduct={foodproduct}
                        showAddToCartButton={false}
                        cartUpdate={true}
                        showRemoveProductButton={true}
                        setRun={setRun}
                        run={run}
                    />
                ))}
            </div>
        );
    };

    const noItemsMessage = () => (
        <h3>
            Your cart is empty. <br /> <Link to="/shop">Continue shopping</Link>
        </h3>
    );

    return (
        <div className="container">
            <div className="row">
                <div className="col-md-6 col-sm-12  mt-4">{items.length > 0 ? showItems(items) : noItemsMessage()}</div>

                <div className="col-md-6 col-sm-12 mb-3">
                    <br/>
                    <h2 className="mb-4 mt-4">Your cart summary</h2>
                    
                     <Checkout foodproducts={items} setRun={setRun} run={run} /> 
                </div>
            </div>
        </div>
    );
};


export default Cart;