import React, { Fragment } from "react";
import { Link, withRouter } from "react-router-dom";
import { signout, isAuthenticated } from "../auth";
import { itemTotal } from "./cartHelpers";


const isActive = (history, path) => {
    if (history.location.pathname === path) {
        return { color: "#ff9900" };
    } else {
        return { color: "black" };
    }
};

const Menu = ({ history }) => (
    <div >
        <ul style={{padding:"5px"}} className="nav nav-tabs bg-danger">
            <li>
                <div  style={{padding:"2px 10px",fontSize:"20px",color:"red",border:"1px solid white",
                borderRadius:"50%",backgroundColor:"white",margin:"0px 50px"}}>
                e!
                </div>
                
            </li>
            <li className="nav-item">
                <Link
                    className="nav-link text-white"
                    style={isActive(history, "/")}
                    to="/"
                >
                    Home
                </Link>
            </li>

            <li className="nav-item">
                <Link
                    className="nav-link text-white"
                    style={isActive(history, "/shop")}
                    to="/shop"
                >
                    Menu
                </Link>
            </li>
            {/* <li className="nav-item">
                <Link
                    className="nav-link text-white"
                    style={isActive(history, "/place")}
                    to="/place"
                >
                    Place
                </Link>
            </li> */}

            <li className="nav-item">
                <Link
                    className="nav-link text-white"
                    style={isActive(history, "/cart")}
                    to="/cart"
                >
                    Cart{" "}
                    <sup style={{backgroundColor:"black",padding:"2px 5px", borderRadius:"50%" }}>
                        <small className="cart-badge" >{itemTotal()}</small>
                    </sup>
                </Link>
            </li>

            {isAuthenticated() && isAuthenticated().user.role === 0 && (
                <li className="nav-item">
                    <Link
                        className="nav-link text-white"
                        style={isActive(history, "/user/dashboard")}
                        to="/user/dashboard"
                    >
                        Dashboard
                    </Link>
                </li>
            )}

            {isAuthenticated() && isAuthenticated().user.role === 1 && (
                <li className="nav-item">
                    <Link
                        className="nav-link text-white"
                        style={isActive(history, "/admin/dashboard")}
                        to="/admin/dashboard"
                    >
                        Dashboard
                    </Link>
                </li>
            )}

            {!isAuthenticated() && (
                <Fragment>
                    <li className="nav-item">
                        <Link
                            className="nav-link text-white"
                            style={isActive(history, "/signin")}
                            to="/signin"
                        >
                            Signin
                        </Link>
                    </li>

                    <li className="nav-item">
                        <Link
                            className="nav-link text-white"
                            style={isActive(history, "/signup")}
                            to="/signup"
                        >
                            Signup
                        </Link>
                    </li>
                </Fragment>
            )}

            {isAuthenticated() && (
                <li className="nav-item">
                    <span
                        className="nav-link"
                        style={{ cursor: "pointer", color: "black" }}
                        onClick={() =>
                            signout(() => {
                                history.push("/");
                            })
                        }
                    >
                        Signout
                    </span>
                </li>
            )}
        </ul>
    </div>
);

export default withRouter(Menu);