import React, { useState, useEffect } from "react";
import Layout from "./Layout";
import RCard from "./Rcard";
import { getCategories, getFilteredPlaces } from "./apiPlace";
import "../../styles/place.css"

const Locate = () => {
    const [myFilters, setMyFilters] = useState({
        filters: { category: [], price: [] }
    });
    const [categories, setCategories] = useState([]);
    const [error, setError] = useState(false);
    const [limit, setLimit] = useState(6);
    const [skip, setSkip] = useState(0);
    const [size, setSize] = useState(0);
    const [filteredResults, setFilteredResults] = useState([]);

    const prices=[
        {
            _id:0,
            name:"Any",
            array:[]
    
        },
        {
            _id:1,
            name:"₹10-₹100",
            array:[10,100]
    
        },
        {
            _id:2,
            name:"₹101-₹200",
            array:[101,200]
    
        },
        {
            _id:3,
            name:"₹201-₹500",
            array:[201,500]
    
        },
        {
            _id:4,
            name:"₹501-₹1000",
            array:[501,1000]
    
        }
    ]
    
    



    const Checkbox=({categories,handleFilters})=> {
        const [checked,setChecked]= useState([])
    
        const handleToggle=c=>()=>{
            const currentCategoryId=checked.indexOf(c)
            const newCheckedCategoryId= [...checked]
    
            if(currentCategoryId===-1){
                newCheckedCategoryId.push(c)
            }else{
                newCheckedCategoryId.splice(currentCategoryId,1)
            }
            console.log(newCheckedCategoryId)
            setChecked(newCheckedCategoryId)
            handleFilters(newCheckedCategoryId)
        }
        return categories.map((c,i)=> (
            <li key={i} className="List-unstyled">
                <input onChange={handleToggle(c._id)} value={checked.indexOf(c._id===-1)} type="checkbox"className="form-check-input"/>
                <label className="form-check-label">{c.name}</label>
            </li>
        ));
    };


    const RadioBox = ({ prices, handleFilters }) => {
       
        const handleChange = event => {
            handleFilters(event.target.value);
            
        };
    
        return prices.map((p, i) => (
            <div key={i}>
                <input
                    onChange={handleChange}
                    value={`${p._id}`}
                    name={p}
                    type="radio"
                    className="mr-2 ml-4"
                />
                <label className="form-check-label">{p.name}</label>
            </div>
        ));
    };


    const init = () => {
        getCategories().then(data => {
            if (data.error) {
                setError(data.error);
            } else {
                setCategories(data);
            }
        });
    };
    

    const loadFilteredResults = newFilters => {
        // console.log(newFilters);
        getFilteredPlaces(skip, limit, newFilters).then(data => {
            if (data.error) {
                setError(data.error);
            } else {
                setFilteredResults(data.data);
                 setSize(data.size);
                 setSkip(0);
            }
        });
    };

    const loadMore = () => {
        let toSkip = skip + limit;
        // console.log(newFilters);
        getFilteredPlaces(toSkip, limit, myFilters.filters).then(data => {
            if (data.error) {
                 setError(data.error);
            } else {
                 setFilteredResults([...filteredResults, ...data.data]);
                setSize(data.size);
                setSkip(toSkip);
             }
         });
     };

     const loadMoreButton = () => {
         return (
            size > 0 &&
             size >= limit && (
                 <button onClick={loadMore} className="btn btn-warning mb-5">
                     Load more
                 </button>
             )
         );
     };

    useEffect(() => {
        init();
        loadFilteredResults(skip, limit, myFilters.filters);
    }, []);

    const handleFilters = (filters, filterBy) => {
        // console.log("SHOP", filters, filterBy);
        const newFilters = { ...myFilters };
        newFilters.filters[filterBy] = filters;

        if (filterBy === "price") {
            let priceValues = handlePrice(filters);
            newFilters.filters[filterBy] = priceValues;
        }
        loadFilteredResults(myFilters.filters);
        setMyFilters(newFilters);
    };

    const handlePrice = value => {
        const data = prices;
        let array = [];

        for (let key in data) {
            if (data[key]._id === parseInt(value)) {
                array = data[key].array;
            }
        }
        return array;
    };

    return (
        <div className="container-fluid" >
            <div className="row" >
                <div className="col-sm-12 col-md-4 " style={{padding:"50px 20px"}}>
                    
                    <div className="Filterrectangle" style={{padding:"50px 20px"}} >
                    <div className="FilterFilt">Filters</div>
                    <div  className="Filter-Select-Location" >Select Location</div>
                    <div>
                        <select className="Filterdropdown" >
                           <option style={{color: '#8c96ab'}} value="0">Select</option>
                             {/* { locations.map((item) => {
                                return <option value={item.location_id} >{ `${item.name}, ${item.city}`  }</option>
                            }) }   */}
                        </select>
                    </div>
                    
                    <h4>Filter by categories</h4>
                    <ul>
                        <Checkbox
                            categories={categories}
                            handleFilters={filters =>
                                handleFilters(filters, "category")
                            }
                        />
                    </ul>

                    <h4>Filter by price range</h4>
                    <div>
                        <RadioBox
                            prices={prices}
                            handleFilters={filters =>
                                handleFilters(filters, "price")
                            }
                        />
                    </div>
                    
                    </div>
                   
                </div>

                <div className="col-sm-8 col-md-8 col-lg-8">
                    <h2 className="mb-4">Places</h2>
                    <div className="row">
                        {filteredResults.map((place, i) => (
                            <div key={i} className="col-lg-4 col-md-6 col-sm-12  mt-3">
                                <RCard  place={place} />
                            </div>
                            
                        ))}
                    </div>
                    
                    {loadMoreButton()}
                </div>
            </div>
        </div>
    );
};

export default Locate;