import React from "react";
import { API } from "../../config";

const ShowImage = ({ item, url,page }) => (
    <div className="food-img" >
        <img
            src={`${API}/${url}/${page}/${item._id}`}
            alt={item.name}
            className=" mb-3"
            style={{ maxHeight: "100%", maxWidth: "100%",borderRadius:"5%" }}
        />
    </div>
);

export default ShowImage;

