import React  from "react";
import { BrowserRouter , Switch ,Route} from "react-router-dom";
import Home from "./Components/core/Home"
import Signup from "./Components/user/Signup";
import Signin from "./Components/user/Signin";
import Menu from "./Components/core/Menu";
import PrivateRoute from './Components/auth/PrivateRoute';
import Dashboard from './Components/user/UserDashboard';
import AdminRoute from './Components/auth/AdminRoute';
import AdminDashboard from './Components/user/AdminDashboard';
import AddCategory from './Components/admin/AddCategory';
import AddFoodProduct from './Components/admin/AddFoodProduct';
import AddPlace from './Components/admin/AddPlace';
import Shop from "./Components/core/Shop";
import Locate from "./Components/core/place";
import FoodProduct from './Components/core/FoodProduct';
import Cart from "./Components/core/Cart";
import Orders from "./Components/admin/Order";
import ManageFoodProducts from "./Components/admin/ManageFoodProducts";
import UpdateFoodProduct from "./Components/admin/UpdateFoodProducts";


const Routes=()=>{
    return (
        <BrowserRouter>
        <Menu/>
            <Switch>
                <Route path="/" exact component={Home}/>
                <Route path="/shop" exact component={Shop}/>
                <Route path="/place" exact component={Locate}/>
                <Route path="/signup" exact component={Signup}/>
                <Route path="/signin" exact component={Signin}/>
                <PrivateRoute path="/user/dashboard" exact component={Dashboard}/>
                <AdminRoute path="/admin/dashboard" exact component={AdminDashboard} />
                <AdminRoute path="/create/category" exact component={AddCategory} />
                <AdminRoute path="/create/foodproduct" exact component={AddFoodProduct} />
                <AdminRoute path="/create/place" exact component={AddPlace} />
                <Route path="/foodproduct/:foodproductId" exact component={FoodProduct} />
                <Route path="/cart" exact component={Cart}/>
                <AdminRoute path="/admin/orders" exact component={Orders} />
                <PrivateRoute path="/admin/foodproducts" exact component={ManageFoodProducts} />
                <AdminRoute path="/admin/foodproduct/update/:foodproductId" exact component={UpdateFoodProduct} />
               
    



                
        </Switch>
        </BrowserRouter>
    )
}


export default Routes;