import React, { useState, useEffect } from "react";
import Layout from "../core/Layout";
import { isAuthenticated } from "../auth";
import { Link } from "react-router-dom";
import { getFoodProducts, deleteFoodProduct } from "./apiAdmin";

const ManageFoodProducts = () => {
    const [foodproducts, setFoodProducts] = useState([]);

    const { user, token } = isAuthenticated();

    const loadFoodProducts = () => {
        getFoodProducts().then(data => {
            if (data.error) {
                console.log(data.error);
            } else {
                setFoodProducts(data);
            }
        });
    };

    const destroy = foodproductId => {
        deleteFoodProduct(foodproductId, user._id, token).then(data => {
            if (data.error) {
                console.log(data.error);
            } else {
                loadFoodProducts();
            }
        });
    };

    useEffect(() => {
        loadFoodProducts();
    }, []);

    return (
        <Layout
            title="Manage Products"
            description="Perform CRUD on products"
            className="container-fluid"
        >
            <div className="row">
                <div className="col-12">
                    <h2 className="text-center">
                        Total {foodproducts.length} products
                    </h2>
                    <hr />
                    <ul className="list-group">
                        {foodproducts.map((p, i) => (
                            <li
                                key={i}
                                className="list-group-item d-flex justify-content-between align-items-center"
                            >
                                <strong className="col-4">{p.name}</strong>
                                <Link to={`/admin/foodproduct/update/${p._id}`}>
                                    <span className="badge badge-warning badge-pill">
                                        Update
                                    </span>
                                </Link>
                                <span
                                    onClick={() => destroy(p._id)}
                                    className="badge badge-danger badge-pill"
                                >
                                    Delete
                                </span>
                            </li>
                        ))}
                    </ul>
                    <br />
                </div>
            </div>
        </Layout>
    );
};

export default ManageFoodProducts;