import React, { useState, useEffect } from 'react';
import Layout from '../core/Layout';
import { isAuthenticated } from '../auth'; 
import { Link, Redirect } from 'react-router-dom';
import { getFoodProduct, getCategories, updateFoodProduct } from './apiAdmin';

const UpdateFoodProduct = ({ match }) => {
    const [values, setValues] = useState({
        name:'',
        restaurantName: '',
        restaurantAddress:'',
        city:'',
        description:'',
        price:'',
        sold:'',
        foodImage: '',
        category :'',
        categories:[],
        loading : false,
        error:'',
        createdFoodProduct: '',
        redirectToProfile :false,
        formData : '' 

        
    });
    const [categories, setCategories] = useState([]);

    const { user, token } = isAuthenticated();
    const {
        name,
        restaurantName,
        restaurantAddress,
        city,
        description,
        price,
        sold,
        loading,
        error,
        createdFoodProduct,
        redirectToProfile,
        formData
    } = values;

    const init = foodproductId => {
        getFoodProduct(foodproductId).then(data => {
            if (data.error) {
                setValues({ ...values, error: data.error });
            } else {
                // populate the state
                setValues({
                    ...values,
                    name: data.name,
                    description: data.description,
                    price: data.price,
                    category: data.category._id,

                    restaurantName:data.restaurantName,

                    restaurantAddress:data.restaurantAddress,
                    city:data.city,
                    sold:data.sold,                   
                    formData: new FormData()
                });
                // load categories
                initCategories();
            }
        });
    };

    // load categories and set form data
    const initCategories = () => {
        getCategories().then(data => {
            if (data.error) {
                setValues({ ...values, error: data.error });
            } else {
                setCategories(data);
            }
        });
    };

    useEffect(() => {
        init(match.params.foodproductId);
    }, []);

    const handleChange = name => event => {
        const value = name === 'foodImage' ? event.target.files[0] : event.target.value;
        formData.set(name, value);
        setValues({ ...values, [name]: value });
    };

    const clickSubmit = event => {
        event.preventDefault();
        setValues({ ...values, error: '', loading: true });

        updateFoodProduct(match.params.foodproductId, user._id, token, formData).then(data => {
            if (data.error) {
                setValues({ ...values, error: data.error });
            } else {
                setValues({
                    ...values,
                    name: '',
                    description: '',
                    restaurantName:'',
                    restaurantAddress:'',
                    city:'',
                    foodImage: '',
                    price: '',
                    city: '',
                    sold:'',
                    loading: false,
                    error: false,
                    redirectToProfile: true,
                    createdFoodProduct: data.name
                });
            }
        });
    };

    const newPostForm = () => (
        <form className="mb-3" onSubmit={clickSubmit}>
            <h4>
                Add Image
            </h4>
            <div className="form-group">
                <label className="btn btn-secondary">
                    <input onChange={handleChange('foodImage')}type="file"  name="foodImage"accept="image/*"/>
                </label>
            </div>



            <div className="form-group">
                <label className="text-muted">Name</label>
                <input onChange={handleChange('name')} type="text" className="form-control" value={name} />
            </div>


            <div className="form-group">
                <label className="text-muted">Restaurant Name</label>
                <input onChange={handleChange('restaurantName')} type="text" className="form-control" value={restaurantName} />
            </div>


            <div className="form-group">
                <label className="text-muted">Restaurant Address</label>
                <input onChange={handleChange('restaurantAddress')} type="text" className="form-control" value={restaurantAddress} />
            </div>
            <div className="form-group">
                <label className="text-muted">Restaurant City</label>
                <input onChange={handleChange('city')} type="text" className="form-control" value={city} />
            </div>

            <div className="form-group">
                <label className="text-muted">Description</label>
                <textarea onChange={handleChange('description')} className="form-control" value={description} />
            </div>

            <div className="form-group">
                <label className="text-muted">Price</label>
                <input onChange={handleChange('price')} type="number" className="form-control" value={price} />
            </div>
            <div className="form-group">
                <label className="text-muted">Sold</label>
                <input onChange={handleChange('sold')} type="number" className="form-control" value={sold} />
            </div>

            <div className="form-group">
                <label className="text-muted">Category</label>
                <select onChange={handleChange('category')} className="form-control">
                    <option>Please select</option>
                    {categories &&
                        categories.map((c, i) => (
                            <option key={i} value={c._id}>
                                {c.name}
                            </option>
                        ))}
                </select>
            </div>

            <button className="btn btn-outline-primary">Update Product</button>
        </form>
    );

    const showError = () => (
        <div className="alert alert-danger" style={{ display: error ? '' : 'none' }}>
            {error}
        </div>
    );

    const showSuccess = () => (
        <div className="alert alert-info" style={{ display: createdFoodProduct ? '' : 'none' }}>
            <h2>{`${createdFoodProduct}`} is updated!</h2>
        </div>
    );

    const showLoading = () =>
        loading && (
            <div className="alert alert-success">
                <h2>Loading...</h2>
            </div>
        );

    const redirectUser = () => {
        if (redirectToProfile) {
            if (!error) {
                return <Redirect to="/" />;
            }
        }
    };

    return (
        <Layout title="Update Your product" description={`G'day ${user.name}, ready to Update product?`}>
            <div className="row">
                <div className="col-md-8 offset-md-2">
                    {showLoading()}
                    {showSuccess()}
                    {showError()}
                    {newPostForm()}
                    {redirectUser()}
                </div>
            </div>
        </Layout>
    );
};

export default UpdateFoodProduct;