import React, { useState } from 'react';
import { Link, Redirect } from 'react-router-dom';
import ShowImage from './ShowImage';
import { addItem, updateItem, removeItem } from './cartHelpers';



const RCard=({
            place,
            showViewplaceButton=true,
            showAddToCartButton = true,
            cartUpdate = false,
            showRemoveplaceButton = false,
            setRun = f => f,
            run = undefined})=> {

                const [redirect, setRedirect] = useState(false);
                const [count, setCount] = useState(place.count);
                
                const showViewButton=showViewplaceButton=>{
                return(
                        showViewplaceButton && (
                        <Link className="btn btn-outline-primary mr-2" to= {`/place/${place._id}`}>
                            View Palace
                        </Link>
                        )
                    )
                }
                const addToCart = () => {
                    // console.log('added');
                    addItem(place, setRedirect(true));
                  };
                
                  const shouldRedirect = redirect => {
                    if (redirect) {
                      return <Redirect to="/cart" />;
                    }
                  };
                
                  const showAddToCartBtn = showAddToCartButton => {
                    return (
                      showAddToCartButton && (
                        <button  onClick={addToCart} className="btn btn-outline-warning  mb-2 mt-2 card-btn-1  ">
                          Add to cart
                        </button>
                      )
                    );
                  };

                  const handleChange = placeId => event => {
                    setRun(!run); // run useEffect in parent Cart
                    setCount(event.target.value < 1 ? 1 : event.target.value);
                    if (event.target.value >= 1) {
                      updateItem(placeId, event.target.value);
                    }
                  };


                  const showCartUpdateOptions = cartUpdate => {
                    return (
                      cartUpdate && (
                        <div>
                          <div className="input-group mb-3">
                            <div className="input-group-prepend">
                              <span className="input-group-text"> Quantity</span>
                            </div>
                            <input type="number" className="form-control " value={count} onChange={handleChange(place._id)} />
                          </div>
                        </div>
                      )
                    );
                  };

                  const showRemoveButton = showRemoveplaceButton => {
                    return (
                      showRemoveplaceButton && (
                        <button
                          onClick={() => {
                            removeItem(place._id);
                            setRun(!run); // run useEffect in parent Cart
                          }}
                          className="btn btn-outline-danger mt-2 mb-3"
                        >
                          Remove Food
                        </button>
                      )
                    );
                  };
    
    return(
       
        
            <div className="card">
                <div className="d-flex  align-items-center justify-content-center card-header" style={{'fontSize':'19px'}}>{place.restaurantName}</div>
                <div className="card-body">
                {shouldRedirect(redirect)}
                
                <ShowImage  item={place} url="place" page="placeImage"/>
                    <p className="black-9" style={{'fontSize':'17px', fontWeight: 'bold', fontStyle: 'italic' }}>Rating: {place.rating}</p>
                    <p className="black-8"style={{'fontSize':'17px'}}>Price : ₹{place.price}</p>
                    <p className="black-8">Restaurant : {place.restaurantAddress}</p>
                    
                    <div>
                    {showViewButton(showViewplaceButton)}
                    {showAddToCartBtn(showAddToCartButton)}
                    
                    
                    {showRemoveButton(showRemoveplaceButton)}
                    </div>
                    {showCartUpdateOptions(cartUpdate)}
                    
                </div>
            </div>
        
    )
}
export default RCard;