import React, { useState } from 'react';
import { Link, Redirect } from 'react-router-dom';
import ShowImage from './ShowImage';
import { addItem, updateItem, removeItem } from './cartHelpers';
import "../../styles/Card.css"




const Card=({
            foodproduct,
            showViewProductButton=true,
            showAddToCartButton = true,
            cartUpdate = false,
            showRemoveProductButton = false,
            setRun = f => f,
            run = undefined})=> {

                const [redirect, setRedirect] = useState(false);
                const [count, setCount] = useState(foodproduct.count);
                
                const showViewButton=showViewProductButton=>{
                return(
                        showViewProductButton && (
                        <Link className="btn btn-outline-primary  mb-2 mt-2 card-btn-1 mr-2" to= {`/foodproduct/${foodproduct._id}`}>
                            View Food
                        </Link>
                        )
                    )
                }
                const addToCart = () => {
                    // console.log('added');
                    addItem(foodproduct, setRedirect(true));
                  };
                
                  const shouldRedirect = redirect => {
                    if (redirect) {
                      return <Redirect to="/cart" />;
                    }
                  };
                
                  const showAddToCartBtn = showAddToCartButton => {
                    return (
                      showAddToCartButton && (
                        <button onClick={addToCart} className=" btn btn-outline-warning  mb-2  mt-2 card-btn-1  ">
                          Add to cart
                        </button>
                      )
                    );
                  };

                  const handleChange = foodproductId => event => {
                    setRun(!run); // run useEffect in parent Cart
                    setCount(event.target.value < 1 ? 1 : event.target.value);
                    if (event.target.value >= 1) {
                      updateItem(foodproductId, event.target.value);
                    }
                  };


                  const showCartUpdateOptions = cartUpdate => {
                    return (
                      cartUpdate && (
                        <div>
                          <div className="input-group mb-3">
                            <div className="input-group-prepend">
                              <span className="input-group-text"> Quantity</span>
                            </div>
                            <input type="number" className="form-control " value={count} onChange={handleChange(foodproduct._id)} />
                          </div>
                        </div>
                      )
                    );
                  };

                  const showRemoveButton = showRemoveProductButton => {
                    return (
                      showRemoveProductButton && (
                        <button
                          onClick={() => {
                            removeItem(foodproduct._id);
                            setRun(!run); // run useEffect in parent Cart
                          }}
                          className="btn btn-outline-danger  mb-2  mt-2 "
                        >
                          Remove Food
                        </button>
                      )
                    );
                  };
    
    return(
       
        
            <div className="card food-card"  >
                <div className="d-flex  align-items-center justify-content-center card-header" style={{'fontSize':'19px',color:"#192F60"}}>{foodproduct.name}</div>
                <div className="card-body food-card-body">
                {shouldRedirect(redirect)}
                <div className="card-material">
                
                <ShowImage  item={foodproduct} url="foodproduct" page="foodImage"/>
                    <p className="card-description">{foodproduct.description.substring(0, 30)}</p>
                    <p className="card-price"style={{'fontSize':'17px'}}>Price : ₹{foodproduct.price}</p>
                    <p className="card-restaurent">Restaurant : {foodproduct.restaurantName}</p>

                  </div>
                    
                    <div className='d-flex  align-items-center justify-content-center'>
                    {showViewButton(showViewProductButton)}
                    {showAddToCartBtn(showAddToCartButton)}
                    
                    
                    {showRemoveButton(showRemoveProductButton)}
                  </div>
                    {showCartUpdateOptions(cartUpdate)}
                    

                </div>
            </div>
        
    )
}
export default Card;