import React, { useState, useEffect } from 'react';
import Layout from './Layout';
import { read, listRelated } from './apiCore';
import Card from './Card';

const FoodProduct = props => {
    const [foodproduct, setFoodProduct] = useState({});
    const [relatedFoodProduct, setRelatedFoodProduct] = useState([]);
    const [error, setError] = useState(false);

    const loadSingleFoodProduct = foodproductId => {
        read(foodproductId).then(data => {
            if (data.error) {
                setError(data.error);
            } else {
                setFoodProduct(data);
                // fetch related products
                listRelated(data._id).then(data => {
                    if (data.error) {
                        setError(data.error);
                    } else {
                        setRelatedFoodProduct(data);
                    }
                });
            }
        });
    };

    useEffect(() => {
        const foodproductId = props.match.params.foodproductId;
        loadSingleFoodProduct(foodproductId);
    }, [props]);

    return (
        <Layout
            title={foodproduct && foodproduct.name}
            description={foodproduct && foodproduct.description && foodproduct.description}
            className="container "
        >
            <div className="row mt-4">
                <div className="col-md-6 col-sm-12 mt-5 mb-3 "style={{ padding:"0px" }}>
                    {foodproduct && foodproduct.description && <Card foodproduct={foodproduct} showViewProductButton={false} />}
                </div>

                <div className="col-md-6 col-sm-12 " style={{ padding:"0px 0px" }}>
                    <h4 className='ml-4' style={{ color:"white" }}>Related products</h4>
                    {relatedFoodProduct.map((p, i) => (
                        <div className="col-12 mb-3 mt-3" key={i}>
                            <Card foodproduct={p} />
                        </div>
                    ))}
                </div>
            </div>
        </Layout>
    );
};

export default FoodProduct;