import React, { useState, useEffect } from "react";
import Layout from "./Layout";
import Card from "./Card";
import { list } from "./apiCore";
import { getCategories, getFilteredProducts } from "./apiCore";
import Checkbox from "./CheckBox";
import RadioBox from "./RadioBox";


const Shop = () => {
    const [myFilters, setMyFilters] = useState({
        
        filters: { category:[], price: [] }
    });
    const [data, setData] = useState({
        
        category: "",
        
        results: []
       
    });

    const {  category ,results } = data;
    const selectCategory=()=>{
        const savedCategory=JSON.parse(localStorage.getItem("category"))
        list({  category: savedCategory }).then(
            response => {
                if (response.error) {
                    console.log(response.error);
                } else {
                    setData({ ...data, results: response, searched: true });
                }
            }
        );
    }
    

    const [categories, setCategories] = useState([]);
    const [error, setError] = useState(false);
    const [limit, setLimit] = useState(6);
    const [skip, setSkip] = useState(0);
    const [size, setSize] = useState(0);
    const [filteredResults, setFilteredResults] = useState([]);


   


    

    const init = () => {
       
        
        getCategories().then(data => {
            if (data.error) {
                setError(data.error);
            } else {
                setCategories(data);
            }
        });
    };
     const prices=[
        {
            _id:0,
            name:"Any",
            array:[]
    
        },
        {
            _id:1,
            name:"₹10-₹100",
            array:[10,100]
    
        },
        {
            _id:2,
            name:"₹101-₹200",
            array:[101,200]
    
        },
        {
            _id:3,
            name:"₹201-₹500",
            array:[201,500]
    
        },
        {
            _id:4,
            name:"₹501-₹1000",
            array:[501,1000]
    
        }
    ]
   

    const loadFilteredResults = newFilters => {
        
        
           
            getFilteredProducts(skip, limit, newFilters).then(data => {
                if (data.error) {
                    setError(data.error);
                } else {
                    setFilteredResults(data.data);
                     setSize(data.size);
                     setSkip(0);
                }
            });
         };

    const loadMore = () => {
        let toSkip = skip + limit;
        
        // console.log(newFilters);
        getFilteredProducts(toSkip, limit, myFilters.filters).then(data => {
            if (data.error) {
                 setError(data.error);
            } else {
                 setFilteredResults([...filteredResults, ...data.data]);
                setSize(data.size);
                setSkip(toSkip);
             }
         });
     };

     const loadMoreButton = () => {
         return (
            size > 0 &&
             size >= limit && (
                 <button onClick={loadMore} className="btn btn-warning  mb-5 mt-4">
                     Load more
                 </button>
             )
         );
     };

    useEffect(() => {
        init();
        loadFilteredResults(skip, limit, myFilters.filters);
    }, []);

    const handleFilters = (filters, filterBy) => {
        // console.log("SHOP", filters, filterBy);
        const newFilters = { ...myFilters };
        newFilters.filters[filterBy] = filters;

        if (filterBy === "price") {
            let priceValues = handlePrice(filters);
            newFilters.filters[filterBy] = priceValues;
        }
        loadFilteredResults(myFilters.filters);
        setMyFilters(newFilters);
    };

    const handlePrice = value => {
        const data = prices;
        let array = [];

        for (let key in data) {
            if (data[key]._id === parseInt(value)) {
                array = data[key].array;
            }
        }
        return array;
    };

    return (
        <div className="container-fluid" style={{padding:"30px 30px"}}>
            <div className="row" >
                <div className="col-md-3 col-sm-12 " >
                <h2 className=" ml-4 mt-2">Filters</h2>
                    <div className="filter-part mt-4 "  style={{backgroundColor:"#eef0f3",width:"fit-content",boxShadow:" 0px 3px 6px #00000029" ,padding:"40px 30px",marginLeft:"20px",borderRadius:"2%"}}>
                    
                    <h4>Filter by categories</h4>
                    <ul>
                        <Checkbox 
                            categories={categories}
                            handleFilters={filters =>
                                handleFilters(filters, "category")
                            }
                        />
                    </ul>

                    <h4>Filter by price range</h4>
                    <div>
                        <RadioBox
                            prices={prices}
                            handleFilters={filters =>
                                handleFilters(filters, "price")
                            }
                        />
                    </div>
                    
                    </div>
                   
                </div>

                <div className="col-md-9 col-sm-10 col-xs-12 mt-2">
                    <h2 className="mb-2">Food Products</h2>
                    <div className="row">
                        {
                        filteredResults.map((foodproduct, i) => (
                            <div key={i} className="col-lg-4 col-md-6 col-sm-12  mt-3">
                                <Card  foodproduct={foodproduct} />
                            </div>
                            
                        ))}
                    </div>
                    
                    {loadMoreButton()}
                </div>
            </div>
        </div>
    );
};

export default Shop;